<template>
  <div id="wrapper">
    <nav class="navbar is-success">
      <div class="navbar-brand">
        <router-link to="/" class="navbar-item"><strong> C.W. Gestão de Energia Fotovoltaica</strong></router-link> 
        <a class="navbar-burger" aria-label="menu" aria-expanded="false" data-target="navbar-menu" @click="showMobileMenu = !showMobileMenu">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
      </div>

      <div class="navbar-end">

        <div class="navbar-menu" id="navbar-menu" v-bind:class="{'is-active': showMobileMenu }">
      
          <div class="navbar-item">
            <div class="buttons">
              <template v-if="$store.state.access!=''">
                <router-link to="/clientes" class="navbar-item">Instalações</router-link>
                <router-link to="/faturas" class="navbar-item">Faturas</router-link>
                <router-link to="/my-account" class="navbar-item">Meu Perfil</router-link>
              </template>
              <template v-else>
                <router-link to="/log-in" class="button is-light">Log in</router-link>
              </template>
            </div>
          </div>
        </div>

      </div>
    </nav>
    <progress v-show=$store.state.isLoading />
    <section class="section">
      <router-view/>
    </section>
    <footer class="footer">
      <p class="has-text-centered">Copyright (c) 2021</p>
    </footer>
  
    <div>
    <h1>Empresas</h1>
    <h1>Análise {{ this.ano }}</h1>
    <ul>
      <li v-for="empresa in empresas" :key="empresa.id">
        {{ empresa.nome }}: {{ empresa.receita_anual }}
      </li>
    </ul>
  </div>
  </div>

</template>

<script>
import axios from 'axios';
export default {
  name: 'HelloWorld',
  props: {
    ano: Number
  },
  data() {
    return {
      empresas: []
    };
  },
  created() {
    console.log(this.$cookies.get('csrftoken'))
    this.fetchItems();
    console.log('fetchItems')
  },
  methods: {
    fetchItems() {
      axios.get('http://localhost:8000/api/empresa/', {
        headers: {
          'X-CSRFToken': this.$cookies.get('csrftoken')
        }
      })
      .then(response => {
        this.empresas = response.data;
      })
      .catch(error => {
        console.error("There was an error fetching the items:", error);
      });
    }
  },
}
</script>

